import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Button from 'Shared/components/Button';
import withTranslations from 'Shared/hoc/withTranslations';
import JobOfferCardAlgolia from 'Shared/components/JobOfferCard/JobOfferCardAlgolia';
import JobOfferCardAlgoliaMobile from 'Shared/components/JobOfferCard/JobOfferCardAlgolia.mobile';
import { JobTypes } from 'Shared/constants/Job';
import algoliasearch from 'algoliasearch';
import { replaceAccentsChars } from '../../../../helpers/replaceAccentsChars';

import INDEX_NAME from 'Shared/constants/Algolia'
class PromotedJobOffersSection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      jobOffers: [],
      indexName: INDEX_NAME.JOB_OFFERS,
    };
  }
  componentDidMount() {
    this.fetchJobOffers();
  }
  fetchJobOffers = async () => {
    // Initialize Algolia client
    const client = algoliasearch(process.env.REACT_APP_ALGOLIA_APP_ID, process.env.REACT_APP_ALGOLIA_API_KEY);
    const index = client.initIndex(`${process.env.REACT_APP_ALGOLIA_PREFIX}_${this.state.indexName}`);
    // Set Algolia query parameters
    const queryOptions = {
      hitsPerPage: 6, // Limit the number of job offers to 6
    };
    // Perform Algolia search
    const { hits } = await index.search('', queryOptions);
    // Update component state with the fetched job offers
    this.setState({ jobOffers: hits });
  };
  get jobOffers() {
    const { isMobile } = this.props;
    const { jobOffers } = this.state;
    const jobTypes = JobTypes();
    return jobOffers.map((jobOffer) =>
      isMobile ? (
        <JobOfferCardAlgoliaMobile
          withShareBtn
          key={jobOffer.id}
          hit={{
            ...jobOffer,
            jobType: replaceAccentsChars(jobOffer.jobType), //jobTypes[jobOffer.jobType],
            country: jobOffer.countryCode,
            organisation: { name: jobOffer.organisation.name, logoUrl: jobOffer.organisation.logoUrl },
          }}
        />
      ) : (
        
        <JobOfferCardAlgolia
          key={jobOffer.id}
          hit={{
            ...jobOffer,
            jobType: replaceAccentsChars(jobOffer.jobType), //jobTypes[jobOffer.jobType],
            country: jobOffer.countryCode,
            organisation: { name: jobOffer.organisation.name, logoUrl: jobOffer.organisation.logoUrl },
          }}
        />
      )
    );
  }
  redirectToJobs = () => this.props.history.push('jobs');
  render() {
    const { t } = this.props;
    // return(<div>test</div>)
    return (
      <div className="promoted-job-offers-section__container-background">
        <div className="container">
          <div className="candidates-page-mobile__padding">
            <div className="promoted-job-offers-section__heading">
              {t('candidatesPage.promotedJobOffersSection.heading')}
            </div>
            <div className="promoted-job-offers-section__list">
              <div className="columns">{this.jobOffers}</div>
              <div className="promoted-job-offers-section__button-container">
                <Button onClick={this.redirectToJobs}>
                  {t('candidatesPage.promotedJobOffersSection.cta')}
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default withRouter(withTranslations(PromotedJobOffersSection));


