import React, { Component } from 'react'
import Helmet from 'react-helmet'
import withTranslations from 'Shared/hoc/withTranslations'
import { withRouter } from 'react-router-dom'
import LazyLoad, { forceCheck as forceCheckForLazyLoadInView } from 'react-lazyload'
import CompanyLogos from 'Shared/components/CompanyLogos'
import JobsIcon from 'Shared/assets/images/vectors/cand-feature-1.svg'
import SupportIcon from 'Shared/assets/images/vectors/cand-feature-2.svg'
import ApplicationIcon from 'Shared/assets/images/vectors/cand-feature-3.svg'
import offsets from 'Shared/constants/LazyLoad'
import CandidateGettingStarted from 'Shared/components/CandidateGettingStarted'
import loadable from 'Shared/helpers/loadable'
import Testimonials from './components/Testimonials'
import HeroSection from './components/HeroSection'
import CoursesSection from './components/CoursesSection'
import PromotedJobOffersSection from 'Shared/connectors/Components/PromotedJobOffersSectionConnector'
import { hasAuthorizedRole } from 'Shared/helpers/User'
import Preloadable from "Shared/helpers/Preloadable"
import * as R from 'ramda'
import './CandidatesPage.scss'

import { EmployersPageLoader } from 'Shared/components/Main/MainLoaders'
const EmployersPageConnector = loadable(() => import(/* webpackChunkName: "EmployersPageConnector" */ 'Shared/connectors/Layout/EmployersPageConnector'), EmployersPageLoader)

const JobsLinksFooter = loadable(() => import(/* webpackChunkName: "CandidatesPageConnector" */ 'Shared/layouts/Footer/components/JobsLinksFooter'))

class CandidatesPage extends Component {

  hasAuth = hasAuthorizedRole(this.props.user.role);
  
  componentDidMount () {
    this.props.fetchCurrentLocationIfNeeded()
    setTimeout(forceCheckForLazyLoadInView, 1000);
  }

  render () {
    const { t, candidateLandingPage } = this.props
    
    return (
      <section>
        <Helmet>
          <title>{candidateLandingPage.metaTitle}</title>
          <meta name='description' content={candidateLandingPage.metaDescription} />
          <meta property='og:title' content={candidateLandingPage.metaTitle} />
          <meta property='og:description' content={candidateLandingPage.metaDescription} />
        </Helmet>
        
          <div className='candidates-container parsing--hidden'>
            <section className='hero-section__candidate-page'>
              <div className='container'>
                <div className='candidates-page-mobile__padding'>
                  <HeroSection candidateLandingPage={candidateLandingPage} />
                </div>
              </div>
            </section>
            {
              R.has('testimonials', candidateLandingPage) &&
              <div className='testimonial-section__container-background'>
                <div className='container'>
                <div className='columns'>
                  <div className='column col-md-12 col-sm-12 col-ml-auto'>
                    {
                      R.has('companyLogos', candidateLandingPage) &&
                      <div className='candidates-container__clients'>
                        <div className='columns'>
                          <div className='column col-12 col-md-12 col-sm-12'>
                            <div className='candidates-container__clients-heading text-center'>
                              {t('candidatesPage.brandsOliv')}
                            </div>
                          </div>
                          <div className='column col-12 col-md-12 col-sm-12'>
                            <div className='candidates-container__clients-logos'>
                              <CompanyLogos companyLogos={candidateLandingPage.companyLogos} />
                            </div>
                          </div>
                        </div>
                      </div>
                    }
                  </div>
                </div>
                <Testimonials testimonials={candidateLandingPage.testimonials} />
                  <div className='candidates-page-mobile__padding'>
                    
                  </div>
                </div>
              </div>
            }
            <section className="candidates-page__benefits-section">
            <div className='container'>
              <div className='candidates-page-mobile__padding'>
                <div className='candidates-container__benefits'>
                  <div className='candidates-container__benefits-heading'>
                    {t('candidatesPage.whyOliv.moreAbout')}
                  </div>
                  <div className='candidates-container__benefits-section'>
                    <div className='columns'>
                      <div className='column col-4 col-md-4 col-sm-12'>
                        <LazyLoad offset={offsets.small}>
                          <img className='candidates-container__icon' src={JobsIcon} />
                        </LazyLoad>
                        <h3 className='candidates-container__benefits-advantage'>
                          {t('candidatesPage.whyOliv.findHeading')}
                        </h3>
                        <p className='candidates-container__benefits-response'>
                          {t('candidatesPage.whyOliv.findHeadingMore')}
                        </p>
                      </div>
                      <div className='column col-4 col-md-4 col-sm-12'>
                        <LazyLoad offset={offsets.small}>
                          <img className='candidates-container__icon' src={ApplicationIcon} />
                        </LazyLoad>
                        <h3 className='candidates-container__benefits-advantage'>
                          {t('candidatesPage.whyOliv.easyHeading')}
                        </h3>
                        <p className='candidates-container__benefits-response'>
                          {t('candidatesPage.whyOliv.easyHeadingMore')}
                        </p>
                      </div>
                      <div className='column col-4 col-md-4 col-sm-12'>
                        <LazyLoad offset={offsets.small}>
                          <img className='candidates-container__icon' src={SupportIcon} />
                        </LazyLoad>
                        <h3 className='candidates-container__benefits-advantage'>
                          {t('candidatesPage.whyOliv.supportHeading')}
                        </h3>
                        <p className='candidates-container__benefits-response'>
                          {t('candidatesPage.whyOliv.supportHeadingMore')}
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            </section>
            <PromotedJobOffersSection />
            {!this.hasAuth && <section className='question-section__candidate-page'>
              <div className='container'>
                <div className='candidates-page-mobile__padding'>
                  <LazyLoad offset={offsets.small}>
                     <CandidateGettingStarted noBackground btnName={t('candidatesPage.ctaButtons.createProfile')} />
                  </LazyLoad>
                </div>
              </div>
            </section>}
            <CoursesSection />
            <JobsLinksFooter />
          </div>
          
          <Preloadable isView={false}>
            <EmployersPageConnector />
          </Preloadable>
        
      </section>
    )
  }
}

CandidatesPage.defaultProps = {
  candidateLandingPage: {
    headline: '',
    description: '',
    metaTitle: '',
    metaDescription: '',
    heroImage: ''
  }
}

export default withRouter(withTranslations(CandidatesPage))



