import React, { Component } from 'react'
import { PrimaryButton } from 'Shared/components/Button'
import LazyLoad from 'react-lazyload'
import offsets from 'Shared/constants/LazyLoad'
import cvCourseImage from 'Shared/assets/images/other/cv_course.jpg'
import interviewCourse from 'Shared/assets/images/other/interview_course.png'
import withTranslations from 'Shared/hoc/withTranslations'
import { ExternalRoutes } from 'Shared/constants/Routes'
import './CoursesSection.scss'
import Slider from 'react-slick'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

class CoursesSection extends Component {
  get courses () {
    const { t } = this.props
    return [
      <div key={'1'} className='column col-6 col-xs-12 col-md-12'>
        <div className='courses-section__card'>
          <LazyLoad offset={offsets.small}>
            <div className='courses-section__card-image'
              style={{ backgroundImage: `url(${cvCourseImage})` }}>
              <div
                className='courses-section__card-image-tag'>{t('candidatesPage.coursesSection.free')}</div>
            </div>
          </LazyLoad>
          <div className='courses-section__card-content'>
            <div
              className='courses-section__card-content-heading'>{t('candidatesPage.coursesSection.cvCourse.heading')}</div>
            <div
              className='courses-section__card-content-description'>{t('candidatesPage.coursesSection.cvCourse.description')}</div>
            <a href={ExternalRoutes.cvCourse} target='_blank'>
              <PrimaryButton
                className='courses-section__card-content-btn'>{t('candidatesPage.coursesSection.viewCourse')}</PrimaryButton>
            </a>
          </div>
        </div>
      </div>,
      <div key={'2'} className='column col-6 col-xs-12 col-md-12'>
        <div className='courses-section__card'>
          <LazyLoad offset={offsets.small}>
            <div className='courses-section__card-image'
              style={{ backgroundImage: `url(${interviewCourse})` }}>
              <div
                className='courses-section__card-image-tag'>{t('candidatesPage.coursesSection.free')}</div>
            </div>
          </LazyLoad>
          <div className='courses-section__card-content'>
            <div
              className='courses-section__card-content-heading'>{t('candidatesPage.coursesSection.interviewCourse.heading')}</div>
            <div
              className='courses-section__card-content-description'>{t('candidatesPage.coursesSection.interviewCourse.description')}</div>
            <a href={ExternalRoutes.interviewCourse} target='_blank'>
              <PrimaryButton
                className='courses-section__card-content-btn'>{t('candidatesPage.coursesSection.viewCourse')}</PrimaryButton>
            </a>
          </div>
        </div>
      </div>
    ]
  }

  getCoursesItems = () => this.props.isMobile ? (
    <Slider
      dots
      infinite
      adaptiveHeight
      speed={500}
      slidesToShow={1}
      slidesToScroll={1}
      arrows={false}
    >{this.courses}</Slider>
  ) : <Slider
  dots
  infinite
  adaptiveHeight
  speed={500}
  slidesToShow={2}
  slidesToScroll={1}
  arrows={true}
>{this.courses}</Slider>

  render () {
    const { t } = this.props

    return (
      <div className='courses-section__container-background'>
        <div className='container'>
          <div className='candidates-page-mobile__padding'>
            <div>
              <div className='courses-section__heading'>{t('candidatesPage.coursesSection.heading')}</div>
              <div className='courses-section__description'>{t('candidatesPage.coursesSection.description')}</div>
            </div>
            <div>
              <div className='columns '>
                {this.getCoursesItems()}
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default withTranslations(withDeviceWidthDetector(CoursesSection))



