export const replaceAccentsChars = (str, charWith='-', regexType='NO_SPECIAL' ) => {

    if(!str) return

    const REGEX_TYPE = {
      'ALL': / /g,
      'NO_SPECIAL': /[^A-Z0-9]/ig,
      'SINGLE_FOR_MULTI': /[^A-Z0-9]+/ig,
    }

    return str.replace(REGEX_TYPE[regexType], charWith).toLowerCase()
}


