import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import LazyLoad from 'react-lazyload';
import offsets from 'Shared/constants/LazyLoad';
import CloudinaryImageSource from 'Shared/components/CloudinaryImageSource';
import { searchStateToUrl } from 'Shared/helpers/querySearch';
import withTranslations from 'Shared/hoc/withTranslations';
import Button from 'Shared/components/Button'
import './HeroSection.scss';

class HeroSection extends Component {
  state = {
    jobsQuery: '',
    buttonToggled: false,
  };

  setJobsQuery = ev => {
    return this.setState({ jobsQuery: searchStateToUrl({ query: ev.target.value, page: 1 }) });
  };

  handleSearch = e => {
    e.preventDefault();
    return this.props.history.push(`/jobs?${this.state.jobsQuery}`);
  };

  handleToggle = () => {
    const { buttonToggled } = this.state;
    this.setState({ buttonToggled: !buttonToggled });

    const url = buttonToggled ? '/' : '/employers';
    this.props.history.push(url);
  };

  render() {
    const { t, candidateLandingPage } = this.props;
    const { buttonToggled } = this.state;

    return (
      <div className='hero-section__container'>

        <div className='hero-section__switch-slider-div'>
        <svg className='svg_arrow fadeLeftArrow' width="55" height="18" viewBox="0 0 55 18" fill="none" xmlns="http://www.w3.org/2000/svg">
          <g clip-path="url(#clip0_161_1427)">
          <path d="M2.56731 12.3352C14.9276 8.32555 28.1169 7.44421 40.8414 10.0865C44.5598 10.8586 48.2149 11.9126 51.7937 13.2035C52.8592 13.5879 54.0812 12.7667 54.3657 11.7026C54.6827 10.5169 53.9748 9.4448 52.9053 9.05903C39.9967 4.40254 26.1438 3.12513 12.6445 5.41991C8.85396 6.06427 5.11883 7.00242 1.45561 8.19078C-1.10172 9.02038 -0.00940755 13.1711 2.56731 12.3352Z" fill="#2F3233"/>
          <path d="M46.815 3.23311L48.9777 7.41438L50.0899 9.56475L50.646 10.6399C50.7184 10.7798 50.7875 11.0038 50.8932 11.1178C50.4629 10.6536 52.0853 9.26656 51.3223 9.54866C50.6487 9.79772 49.9942 10.2727 49.3614 10.6176C48.6693 10.9949 47.9773 11.3722 47.2852 11.7495C45.901 12.504 44.5168 13.2586 43.1327 14.0132C42.165 14.5407 41.7881 16.0057 42.3825 16.9534C43.0169 17.9647 44.2083 18.2886 45.2433 17.7243C46.8582 16.844 48.473 15.9637 50.0879 15.0834C50.8569 14.6642 51.6259 14.245 52.3948 13.8258C53.3549 13.3024 54.3554 12.87 54.8048 11.7754C55.264 10.6571 54.8355 9.58936 54.3187 8.59011L53.0211 6.08135C52.156 4.40883 51.2909 2.73633 50.4258 1.06382C49.9117 0.0697932 48.4878 -0.318539 47.5651 0.292859C46.5818 0.944401 46.265 2.16985 46.815 3.23311Z" fill="#2F3233"/>
          </g>
          <defs>
          <clipPath id="clip0_161_1427">
          <rect width="55" height="18" fill="white"/>
          </clipPath>
          </defs>
        </svg>
          <label className='hero-section__candidate-label'>{t('candidatesPage.switchButtons.firstOption')}</label>
            <label className='hero-section__switch'>
              <input
                type='checkbox'
                className='hero-section__switch-input'
                checked={buttonToggled}
                onChange={this.handleToggle}
              />
              <span className='hero-section__switch-slider' />
            </label>
          <label className='hero-section__employer-label'>{t('candidatesPage.switchButtons.secondOption')}</label>
        </div>

        <div className='columns'>
          <div className='column col-6 col-sm-12'>
            <div className='hero-section__heading-container fadeUpAnimate'>
              <div className='hero-section__heading'>
                {candidateLandingPage.headline}
              </div>
              <div className='hero-section__description'>
                {candidateLandingPage.description}
              </div>
              <div className='hero-section__search'>
                <form onSubmit={this.handleSearch}>
               
                    <div className='col-md-12 col-sm-12'>

                      <div className='hero-section__input-container'>
                        <i className='hero-section__input-icon ion-ios-search-outline' />
                        <input
                          className='hero-section__input'
                          type='text'
                          name='message'
                          onChange={this.setJobsQuery}
                          placeholder={t('candidatesPage.placeholder')}
                        />
  
                        <Button
                          type='submit'
                          className='button button--primary hero-section__submit-input-btn'
                        >
                          {t('candidatesPage.search')}
                        </Button>
                      </div>

                    </div>
                   
               
                </form>
              </div>
            </div>
          </div>
          <div className='column col-6 col-sm-12'>
            {/* <div className='hero-section__image-container'>
              <LazyLoad offset={offsets.small}>
                <CloudinaryImageSource
                  path={candidateLandingPage.heroImage}
                  className='hero-section__image'
                  altText='oliv candidates image'
                />
              </LazyLoad>
            </div> */}
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(withTranslations(HeroSection));


