import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import JobLinksService from 'Shared/services/JobLinksService'

const jobLinksService = new JobLinksService()
const MAX_ITEMS_PER_LIST = 5

class JobsLinksFooter extends Component {
  state = {
    jobLinks: {
      internshipsByDepartment: [],
      internshipsByIndustry: [],
      graduateJobsByDepartment: [],
      graduateJobsByIndustry: []
    }
  }

  async componentDidMount () {
    const jobLinks = await jobLinksService.fetchJobLinksIfNeeded()
    this.setState({ jobLinks })
  }

  get internshipsByDepartment () {
    const jobLinks = [
      <li key='internshipsByDepartment' className='jobs-links-footer__jobs-title'>
        {this.props.t('common.internshipsByDepartment')}
      </li>
    ]

    return jobLinks.concat(this.state.jobLinks['internshipsByDepartment'].slice(0, MAX_ITEMS_PER_LIST).map((item, index) => (
      <li key={`${index}+${item.label}`}>
        <a className='jobs-links-footer__jobs-item' href={item.url}>
          {item.label}
        </a>
      </li>
    )))
  }

  get internshipsByIndustry () {
    const jobLinks = [
      <li key='internshipsByIndustry' className='jobs-links-footer__jobs-title'>
        {this.props.t('common.internshipsByIndustry')}
      </li>
    ]

    return jobLinks.concat(this.state.jobLinks['internshipsByIndustry'].slice(0, MAX_ITEMS_PER_LIST).map((item, index) => (
      <li key={`${index}+${item.label}`}>
        <a className='jobs-links-footer__jobs-item' href={item.url}>
          {item.label}
        </a>
      </li>
    )))
  }

  get graduateJobsByDepartment () {
    const jobLinks = [
      <li key='graduateJobsByDepartment' className='jobs-links-footer__jobs-title'>
        {this.props.t('common.graduateJobsByDepartment')}
      </li>
    ]

    return jobLinks.concat(this.state.jobLinks['graduateJobsByDepartment'].slice(0, MAX_ITEMS_PER_LIST).map((item, index) => (
      <li key={`${index}+${item.label}`}>
        <a className='jobs-links-footer__jobs-item' href={item.url}>
          {item.label}
        </a>
      </li>
    )))
  }

  get graduateJobsByIndustry () {
    const jobLinks = [
      <li key='graduateJobsByIndustry' className='jobs-links-footer__jobs-title'>
        {this.props.t('common.graduateJobsByIndustry')}
      </li>
    ]

    return jobLinks.concat(this.state.jobLinks['graduateJobsByIndustry'].slice(0, MAX_ITEMS_PER_LIST).map((item, index) => (
      <li key={`${index}+${item.label}`}>
        <a className='jobs-links-footer__jobs-item' href={item.url}>
          {item.label}
        </a>
      </li>
    )))
  }

  redirectToFullJobsLinks = (type, category) => () => this.props.history.push(`/jobs-links/${type}/${category}`)

  getSeeMoreButton = (type, category) => (
    <div
      className='jobs-links-footer__see-more'
      onClick={this.redirectToFullJobsLinks(type, category)}
    >
      {this.props.t('common.seeMore')}
    </div>
  )

  render () {
    return (
      <div className='container parsing--hidden job-links-footer__container'>
        <div className='candidates-page-mobile__padding'>
          <div className='columns'>
            <div className='column col-3 col-sm-6 col-xs-12'>
              <ul className='jobs-links-footer__jobs-list'>
                {this.internshipsByDepartment}
              </ul>
              {this.getSeeMoreButton('internships', 'department')}
            </div>
            <div className='column col-3 col-sm-6 col-xs-12'>
              <ul className='jobs-links-footer__jobs-list'>
                {this.internshipsByIndustry}
              </ul>
              {this.getSeeMoreButton('internships', 'industry')}
            </div>
            <div className='column col-3 col-sm-6 col-xs-12'>
              <ul className='jobs-links-footer__jobs-list'>
                {this.graduateJobsByDepartment}
              </ul>
              {this.getSeeMoreButton('graduate-jobs', 'department')}
            </div>
            <div className='column col-3 col-sm-6 col-xs-12'>
              <ul className='jobs-links-footer__jobs-list'>
                {this.graduateJobsByIndustry}
              </ul>
              {this.getSeeMoreButton('graduate-jobs', 'industry')}
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(withTranslations(JobsLinksFooter))



