import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import CandidatesPage from 'Shared/layouts/CandidatesPage'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import { getCandidateLandingPage } from 'Shared/selectors/geolocation'

const mapStateToProps = state => ({
  user: state.user,
  candidateLandingPage: getCandidateLandingPage(state)
})

const mapDispatchToProps = dispatch => ({
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CandidatesPage))



