import { connect } from 'react-redux'
import PromotedJobOffersSection from 'Shared/layouts/CandidatesPage/components/PromotedJobOffersSection'
import { getFirstSixPromotedJobOffers } from 'Shared/selectors/geolocation'

const mapStateToProps = (state, ownProps) => ({
  jobOffers: getFirstSixPromotedJobOffers(state)
})

export default connect(mapStateToProps)(PromotedJobOffersSection)



