import React, { Component } from 'react'

class Preloadable extends Component {
  
    render () {

    const { isView } = this.props;
    const visibility = isView ? { color: 'blue' } : {visibility: 'hidden', position: 'fixed' };

    return (
      <div  className='TTTbT' 
            data-view={"A"} 
            style={visibility}
        >
        {this.props.children}
      </div>
    )
  }
}

export default Preloadable


