import React, { Component, createRef } from 'react'
import { getCloudinaryImage } from 'Shared/helpers/image'
import Slider from 'react-slick'
import ModalPlayer from 'Shared/components/ModalPlayer'
import * as R from 'ramda'
import Button from 'Shared/components/Button'

class Testimonials extends Component {
  constructor(props) {
    super(props)
    this.testimonialImageRef = createRef()
  }

  handleIconClick = () => {
    this.testimonialImageRef.current.click()
  }

  videoTrigger = (image) => (
    <div
      className='testimonial__video--thumbnail'
      style={{backgroundImage: `url(${getCloudinaryImage(image)})`}}
    ></div>
  )

  getVideoTestimonial = (testimonial) => (
    <div key={testimonial.name} className={'testimonial__content--video'}>
      <div className='testimonial_card'>
        <div className={'testimonial__video'}>
          <ModalPlayer
            videoUrl={testimonial.videoUrl}
            customTrigger={
              <React.Fragment>
                {this.videoTrigger(testimonial.image)}
                <span ref={this.testimonialImageRef} />
              </React.Fragment>
            }
          />
        </div>
        <div className='testimonial__description--box'>
          <p className={'testimonial__description--video'}>{`"${testimonial.description}"`}</p>

          <div className='bottom_bar_name'>
            <div>
              <p className={'testimonial__name--video'}>{testimonial.name}</p>
              <p className={'testimonial__jobTitle--video'}>{`${testimonial.jobTitle}  ${testimonial.companyName}`}</p>
            </div>
            {/* <i className='icon-im-circle-play testimonial__video--thumbnail--icon' onClick={this.handleIconClick} /> */}
          </div>
        </div>
      </div>
    </div>
  )

  getImageTestimonial = (testimonial) => (
    <div key={testimonial.name} className={'testimonial__content'}>
      <div>
        <div className={'testimonial__mark '} />
        <p className={'testimonial__description'}>{testimonial.description}</p>
        <div className={'testimonial__image__container'}>
          <div className='testimonial__image' style={{ backgroundImage: `url(${getCloudinaryImage(testimonial.image)})` }} />
        </div>
        <div>
          <p className={'testimonial__name'}>{testimonial.name}</p>
          <p className={'testimonial__jobTitle'}>{`${testimonial.jobTitle} at ${testimonial.companyName}`}</p>
        </div>
      </div>
    </div>
  )

  isUrlNotNil = R.pipe(R.prop('videoUrl'), R.isNil, R.not)
  isUrlNotEmpty = R.pipe(R.prop('videoUrl'), R.isEmpty, R.not)
  isVideoTestimonial = R.allPass([this.isUrlNotNil, this.isUrlNotEmpty])

  get testimonials() {
    return R.map(R.ifElse(this.isVideoTestimonial, this.getVideoTestimonial, this.getImageTestimonial))(this.props.testimonials)
  }

  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 1,
      arrows: false,
      responsive: [
        {
          breakpoint: 1500,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1,
            infinite: true,
          },
        },
        {
          breakpoint: 764,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            initialSlide: 1,
            dots: true,
            arrows: false,
            autoplay: false,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            dots: true,
            arrows: false,
            autoplay: false,
          },
        },
      ],
    }

    return (
      <div className={'testimonial__container'}>
        <Slider {...settings}>
          {this.testimonials}
        </Slider>

        {/* <div className='text-center'>
          <Button className='btn_testimonial--candidate-page'>
            Employee Stories
          </Button>
        </div> */}
      </div>
    )
  }
}

export default Testimonials



